.ContactItem {
	width: 100%;
	display: flex;
	align-items: center;
	&:not(:last-child) {
		margin-bottom: 1rem;
	}
	.right-items {
		margin-left: 1rem;
		h6 {
			font-size: 1.4rem;
			font-weight: 500;
		}
	}
}

.ContactPage {
	display: flex;
	align-items: center;
	margin-bottom: 2rem;
	margin-right: 20px;
	@media screen and (max-width: 1270px) {
		display: block;
	}
}

.title {
	margin-bottom: 4rem;
}

.separator-text {
	margin-bottom: 1rem;
	text-align: center;
}
.comment-box {
	display: grid;
	grid-template-columns: repeat(2, 1fr);
	.write-area {
		width: 90%;
		padding: 1rem 2rem;
		.inputtext {
			text-align: center;
			textarea {
				font-size: medium;
				width: 100%;
				resize: none;
				font-family: "Nunito", sans-serif;
				border: 1px solid #ccc;
				border-radius: 4px;
				box-sizing: border-box;
			}
		}
		.submit-button {
			text-align: center;
		}
	}
	@media screen and (max-width: 1270px) {
		grid-template-columns: repeat(1, 1fr);
		.write-area {
			width: 100%;
			margin-bottom: 1rem;
		}
	}
	.display-area {
		max-height: 13rem;
		overflow-y: scroll;
		padding-left: 0.4rem;
		margin-right: 20px;
		.comment {
			margin-bottom: 0.7rem;
			.comment-info {
				p {
					font-size: 0.8rem;
					color: #a4acc486;
				}
			}
			.comment-body {
				font-size: 1rem;
			}
		}
	}
	.display-area::-webkit-scrollbar {
		width: 4px;
	}
	.display-area::-webkit-scrollbar-thumb {
		background-color: #037fff;
		border-radius: 50px;
	}
	.display-area::-webkit-scrollbar-track {
		background-color: #a4acc4;
	}
}
